"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";
import Link from "next/link";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { Alert, AlertDescription } from "@/components/ui/alert";

interface FormData {
  fullName: string;
  email: string;
  phone: string;
  itemDescription: string;
  estimatedValue: string;
  address: string;
  itemPhoto: File | null;
}

export default function SubmitPage() {
  const router = useRouter();
  const [formData, setFormData] = useState<FormData>({
    fullName: "",
    email: "",
    phone: "",
    itemDescription: "",
    estimatedValue: "",
    address: "",
    itemPhoto: null,
  });
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [error, setError] = useState("");
  const [success, setSuccess] = useState(false);

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0] || null;
    setFormData(prev => ({
      ...prev,
      itemPhoto: file
    }));
  };

  const validateForm = (): boolean => {
    if (!formData.fullName.trim()) {
      setError("Nama lengkap harus diisi");
      return false;
    }
    if (!formData.email.trim() || !formData.email.includes("@")) {
      setError("Email yang valid harus diisi");
      return false;
    }
    if (!formData.phone.trim()) {
      setError("Nomor telepon harus diisi");
      return false;
    }
    if (!formData.itemDescription.trim()) {
      setError("Deskripsi barang harus diisi");
      return false;
    }
    if (!formData.estimatedValue.trim()) {
      setError("Perkiraan nilai barang harus diisi");
      return false;
    }
    if (!formData.address.trim()) {
      setError("Alamat harus diisi");
      return false;
    }
    if (!formData.itemPhoto) {
      setError("Foto barang harus diupload");
      return false;
    }
    return true;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError("");
    
    if (!validateForm()) {
      return;
    }

    setIsSubmitting(true);

    try {
      const formDataToSend = new FormData();
      formDataToSend.append("fullName", formData.fullName);
      formDataToSend.append("email", formData.email);
      formDataToSend.append("phone", formData.phone);
      formDataToSend.append("itemDescription", formData.itemDescription);
      formDataToSend.append("estimatedValue", formData.estimatedValue);
      formDataToSend.append("address", formData.address);
      if (formData.itemPhoto) {
        formDataToSend.append("itemPhoto", formData.itemPhoto);
      }

      const response = await fetch("/api/submit", {
        method: "POST",
        body: formDataToSend,
      });

      const result = await response.json();

      if (!response.ok) {
        throw new Error(result.error || "Terjadi kesalahan saat mengirim pengajuan");
      }

      setSuccess(true);
      // Redirect to status page after 2 seconds
      setTimeout(() => {
        router.push(`/status?id=${result.applicationId}`);
      }, 2000);

    } catch (err) {
      setError(err instanceof Error ? err.message : "Terjadi kesalahan yang tidak diketahui");
    } finally {
      setIsSubmitting(false);
    }
  };

  if (success) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-gray-50 to-white flex items-center justify-center p-4">
        <Card className="w-full max-w-md">
          <CardHeader className="text-center">
            <CardTitle className="text-green-600">Pengajuan Berhasil!</CardTitle>
            <CardDescription>
              Pengajuan gadai Anda telah diterima dan sedang diproses oleh tim kami.
            </CardDescription>
          </CardHeader>
          <CardContent className="text-center">
            <p className="text-sm text-gray-600 mb-4">
              Anda akan dialihkan ke halaman status dalam beberapa detik...
            </p>
            <Link href="/status">
              <Button>Cek Status Sekarang</Button>
            </Link>
          </CardContent>
        </Card>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-50 to-white">
      {/* Header */}
      <header className="border-b bg-white/80 backdrop-blur-sm">
        <div className="container mx-auto px-4 py-4">
          <div className="flex items-center justify-between">
            <Link href="/" className="text-2xl font-bold text-gray-900">
              KOKOGADAI
            </Link>
            <nav className="flex gap-4">
              <Link href="/status">
                <Button variant="outline">Cek Status</Button>
              </Link>
              <Link href="/admin">
                <Button variant="outline">Admin</Button>
              </Link>
            </nav>
          </div>
        </div>
      </header>

      {/* Form Section */}
      <div className="container mx-auto px-4 py-8">
        <div className="max-w-2xl mx-auto">
          <Card>
            <CardHeader>
              <CardTitle>Pengajuan Gadai</CardTitle>
              <CardDescription>
                Lengkapi form di bawah ini untuk mengajukan gadai barang Anda
              </CardDescription>
            </CardHeader>
            <CardContent>
              {error && (
                <Alert className="mb-6 border-red-200 bg-red-50">
                  <AlertDescription className="text-red-800">
                    {error}
                  </AlertDescription>
                </Alert>
              )}

              <form onSubmit={handleSubmit} className="space-y-6">
                <div className="grid md:grid-cols-2 gap-4">
                  <div className="space-y-2">
                    <Label htmlFor="fullName">Nama Lengkap *</Label>
                    <Input
                      id="fullName"
                      name="fullName"
                      type="text"
                      value={formData.fullName}
                      onChange={handleInputChange}
                      placeholder="Masukkan nama lengkap"
                      required
                    />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="email">Email *</Label>
                    <Input
                      id="email"
                      name="email"
                      type="email"
                      value={formData.email}
                      onChange={handleInputChange}
                      placeholder="contoh@email.com"
                      required
                    />
                  </div>
                </div>

                <div className="space-y-2">
                  <Label htmlFor="phone">Nomor Telepon *</Label>
                  <Input
                    id="phone"
                    name="phone"
                    type="tel"
                    value={formData.phone}
                    onChange={handleInputChange}
                    placeholder="08xxxxxxxxxx"
                    required
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="itemDescription">Deskripsi Barang *</Label>
                  <Textarea
                    id="itemDescription"
                    name="itemDescription"
                    value={formData.itemDescription}
                    onChange={handleInputChange}
                    placeholder="Jelaskan detail barang yang akan digadaikan (merk, model, kondisi, dll)"
                    rows={4}
                    required
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="estimatedValue">Perkiraan Nilai Barang (Rp) *</Label>
                  <Input
                    id="estimatedValue"
                    name="estimatedValue"
                    type="number"
                    value={formData.estimatedValue}
                    onChange={handleInputChange}
                    placeholder="5000000"
                    required
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="address">Alamat Lengkap *</Label>
                  <Textarea
                    id="address"
                    name="address"
                    value={formData.address}
                    onChange={handleInputChange}
                    placeholder="Alamat lengkap untuk penjemputan barang"
                    rows={3}
                    required
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="itemPhoto">Foto Barang *</Label>
                  <Input
                    id="itemPhoto"
                    name="itemPhoto"
                    type="file"
                    accept="image/*"
                    onChange={handleFileChange}
                    required
                  />
                  <p className="text-sm text-gray-500">
                    Upload foto barang yang jelas dan berkualitas baik
                  </p>
                </div>

                <div className="flex gap-4 pt-4">
                  <Button
                    type="submit"
                    disabled={isSubmitting}
                    className="flex-1"
                  >
                    {isSubmitting ? "Mengirim..." : "Kirim Pengajuan"}
                  </Button>
                  <Link href="/">
                    <Button type="button" variant="outline">
                      Batal
                    </Button>
                  </Link>
                </div>
              </form>
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  );
}
